<template>
    <section-block title="Редактирование профиля">
        <div class="row">
            <div class="col-lg-6 acenter">
                <div class="space10"></div>
                <img :src="imgDataUrl" class="avatar-upload img-circle">
                <div class="space10"></div>
                <button class="btn btn-default btn-small" @click="toggleShow">Изменить фотографию</button>
                <upload field="img"
                        @crop-success="cropSuccess"
                        @crop-upload-success="cropUploadSuccess"
                        v-model="show"
                        :width="300"
                        :height="300"
                        url="/upload"
                        :params="params"
                        langType="ru"
                        img-format="png"></upload>
            </div>
            <div class="col-lg-18 lborder">
                <div class="form">
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.password}">
                            <input class="input-field" type="password" v-model="form.password"/>
                            <label class="input-label">
                                <span class="input-label-content">Новый пароль</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.password_confirmed}">
                            <input class="input-field" type="password" v-model="form.password_confirmed"/>
                            <label class="input-label">
                                <span class="input-label-content">Подтверждение пароля</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input">
                            <el-select v-model="form.district" placeholder="Выберите район" :class="{'input-filled': form.district}">
                                <el-option-group
                                    v-for="group in districts"
                                    :key="group.label"
                                    :label="group.label">
                                    <el-option
                                        v-for="item in group.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input">
                            <el-select v-model="form.specialty" multiple placeholder="Выберите специализацию">
                                <el-option-group
                                    v-for="group in specialties"
                                    :key="group.label"
                                    :label="group.label">
                                    <el-option
                                        v-for="item in group.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.birthday}">
                            <mask-input class="input-field" type="text" v-model="form.birthday"
                                        :mask="[/[0-3]/, /\d/, ' ', '/', ' ', /[0-1]/, /\d/, ' ', '/', ' ', /[1-2]/, /[9,0]/, /\d/, /\d/]"
                                        :guide="true"
                                        placeholderChar="_"
                                        placeholder="__ / __ / ____"></mask-input>
                            <label class="input-label">
                                <span class="input-label-content">Дата рождения</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.email}">
                            <input class="input-field" type="text" v-model="form.email"/>
                            <label class="input-label">
                                <span class="input-label-content">E-mail</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.phone}">
                            <mask-input class="input-field" type="text" v-model="form.phone"
                                        :mask="['+', /[7-8]/, '(', /\d/, /\d/, /\d/, ')', /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/]"
                                        :guide="true"
                                        placeholderChar="_"
                                        placeholder="+_(___)___-__-__"></mask-input>
                            <label class="input-label">
                                <span class="input-label-content">Телефон</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line" :class="{'input-filled': form.about}">
                        <span class="input">
                            <auto-text class="input-field" v-model="form.about" :autosize="{minRows: 3, maxRows: 10}"></auto-text>
                            <label class="input-label">
                                <span class="input-label-content">Расскажите о себе</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line acenter">
                        <button class="btn btn-primary" @click="save">Сохранить</button>
                        <button class="btn btn-default" @click="back">Назад</button>
                    </div>
                </div>
            </div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import upload from 'vue-image-crop-upload/upload-2.vue';
    import maskInput from 'vue-text-mask'
    import autoText from '../global/auto-textarea.vue'
    export default {
        name: 'edit',
        components: {
            sectionBlock,
            upload,
            maskInput,
            autoText
        },
        data() {
            return {
                form: {
                    password: '',
                    password_confirmed: '',
                    birthday: '',
                    email: 'exsinord@yandex.ru',
                    phone: '+7(964)784-89-05',
                    about: '',
                    district: null,
                    specialty: []
                },
                districts: [
                    {
                        label: 'Восточный округ',
                        options: [
                            {
                                label: 'Новогиреево',
                                value: 1
                            },
                            {
                                label: 'Перово',
                                value: 2
                            },
                            {
                                label: 'Новокосино',
                                value: 3
                            },
                            {
                                label: 'Измайлово',
                                value: 4
                            },
                            {
                                label: 'Сокольники',
                                value: 5
                            },
                            {
                                label: 'Вешняки',
                                value: 6
                            },
                            {
                                label: 'Соколиная гора',
                                value: 7
                            },
                            {
                                label: 'Преображенское',
                                value: 8
                            },
                        ],
                    },
                ],
                specialties: [
                    {
                        label: 'Бытовая электроника',
                        options: [
                            {
                                label: 'Аудио и видео (★)',
                                value: 1
                            },
                            {
                                label: 'Игры, приставки и программы (★)',
                                value: 2
                            },
                            {
                                label: 'Настольные компьютеры (★)',
                                value: 3
                            },
                            {
                                label: 'Оргтехника и расходники (★)',
                                value: 4
                            },
                            {
                                label: 'Планшеты и электронные книги (★)',
                                value: 5
                            },
                            {
                                label: 'Телефоны (★)',
                                value: 6
                            },
                            {
                                label: 'Товары для компьютера (★)',
                                value: 7
                            },
                            {
                                label: 'Фототехника (★)',
                                value: 8
                            },
                        ],
                    },
                ],
                show: false,
                params: {
                    token: '123456798',
                    name: 'avatar'
                },
                imgDataUrl: '/images/avatar.png'
            }
        },
        methods: {
            toggleShow() {
                this.show = !this.show;
            },
            cropSuccess(imgDataUrl, field){
                console.log('-------- crop success --------');
                this.imgDataUrl = imgDataUrl;
            },
            cropUploadSuccess(jsonData, field){
                console.log('-------- upload success --------');
                console.log(jsonData);
                console.log('field: ' + field);
                this.imgDataUrl = imgDataUrl;
            },
            save(){
                console.log('save');
            },
            back(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style>
.avatar-upload {
    width: 150px;
    border: 3px solid #3c8dbc;
}
</style>


